.homepage {
  &__card-wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;

    &::before {
      content: "";
      width: 34px;
      height: 34px;
      border-radius: 50%;
      background: #FFFFFF;
      box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
      display: block;
      position: absolute;
      right: 100%;
      background-image: url(./../../assets/images/slider_left-arrow.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      cursor: grab;
    }

    &::after {
      content: "";
      width: 34px;
      height: 34px;
      position: absolute;
      left: 100%;
      border-radius: 50%;
      background: #FFFFFF;
      box-shadow: 1px 3px 6px rgba(0, 0, 0, 0.129331);
      display: inline-block;
      background-image: url(./../../assets/images/slider_right-arrow.svg);
      background-repeat: no-repeat;
      background-size: contain;
      background-position: center;
      cursor: grab;
    }
  }

  &__top {
    padding-top: 31px;
  }

  &__top-heading {
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    color: #9B9B9B;
    padding-bottom: 10px;
    padding-right: 30px;

  }

  &__info {
    text-align: center;
    padding-top: 21px;
    padding-bottom: 74px;
    border-top: 1px solid rgba(182, 202, 251, 0.606827);
  }

  &__info-btn {
    font-size: 15px;
    line-height: 18px;
    color: #4E72CA;
    border: none;
    outline: none;
    background: none;
    cursor: pointer;
    position: relative;

    &::after {
      content: "";
      position: absolute;
      right: -20px;
      top: 5px;
      bottom: 0;
      margin: auto;
      background-repeat: no-repeat;
      width: 16px;
      height: 10px;
      background-image: url(./../../assets/images/down_arrow.svg)
    }
  }

}

.active {
  font-weight: 900;
  font-size: 24px;
  line-height: 30px;
  color: #4E72CA !important;
}

.loader {
  border: 16px solid #f3f3f3;
  border-radius: 50%;
  border-top: 16px solid #3498db;
  width: 120px;
  height: 120px;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
  }

  100% {
    -webkit-transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}