.footer {
    background-color: #33383c;

    &__top {
        background: #282b30;
        padding: 23px 0;
    }

    &__top-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__top-left {
        display: flex;
        align-items: center;
    }

    &__top-text {
        max-width: 149px;
        width: 100%;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        margin-left: 14px !important;
    }

    &__top-center {
        max-width: 307px;
        width: 100%;
    }

    &__center-text {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #98A4B7;
    }

    &__top-right {
        display: flex;
        align-items: center;
        gap: 5px;
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        padding-bottom: 52px;
    }

    &__bottom-item-1 {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding-top: 52px;
    }

    &__bottom-item-2 {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding-top: 52px;
    }

    &__bottom-item-3 {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding-top: 52px;
    }

    &__bottom-item-4 {
        display: flex;
        flex-direction: column;
        gap: 20px;
        padding-top: 52px;
    }

    &__bottom-title {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #FFFFFF;
        margin-bottom: 12px !important;
        position: relative;

        &::before {
            content: "";
            display: inline-block;
            width: 10px;
            height: 10px;
            position: absolute;
            left: -12px;
            top: 4px;
            background-image: url("./../../assets/images/red_arrow.svg");
            background-repeat: no-repeat;
            background-size: contain;
        }
    }

    &__bottom-link {
        font-weight: 500;
        font-size: 14px;
        line-height: 29px;
        display: block;
        color: #878f99;

        &:hover {
            color: white;
        }
    }

    &__info {
        text-align: center;
        padding-bottom: 34px;
        border-bottom: 1px solid #4c565e;
    }

    &__info-btn {
        font-size: 15px;
        line-height: 18px;
        color: #66717a;
        border: none;
        outline: none;
        background: none;
        cursor: pointer;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            right: -20px;
            top: 2px;
            bottom: 0;
            margin: auto;
            background-repeat: no-repeat;
            width: 16px;
            height: 10px;
            background-image: url(./../../assets/images/down_arrow.svg)
        }
    }

    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 27px 0;
    }

    &__nav-link {
        font-size: 15px;
        line-height: 18px;
        color: #878f99;
        display: block;

        &:hover {
            color: #05f3b3;
        }
    }


}

