.error{
    width: 100%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    padding-bottom: 5%;
    justify-content: space-between;



    &__title{
        font-family: 'Itim', cursive;
        font-weight: 900;
        font-style: normal;
        font-size: 51px;
        letter-spacing: .29em;
        line-height: 1em;
        text-transform: uppercase;
    }

    &__text{
        text-align: center;
        font-family: futura-pt;
        font-weight: 400;
        font-family: 'Source Sans Pro', sans-serif;
        font-style: normal;
        font-size: 22px;
        letter-spacing: 0em;
        line-height: 1.7em;
        text-transform: none;
        white-space: pre-wrap;
        color: rgba(29, 29, 29, .7);
    }
}

