.productCard {
    margin-top: 31px;
    display: flex;
    align-items: stretch;
    justify-content: space-between !important;
    height: auto;
    flex-wrap: wrap;

    &__item {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: space-between;
        width: 265px;
        height: auto;
        margin-bottom: 30px;
        transition: all 0.6s ease;
    }

    &__img {
        width: 262px;
        height: 190px;
        margin-bottom: 13px;
        border-radius: 5px;
        object-fit: contain;
    }

    &__heading {
        font-size: 17px;
        font-weight: 500;
        margin-bottom: 10px;
        padding-right: 3px;
        padding-left: 5px;
        line-height: 19px;
        color: #000000;
        margin-bottom: 15px !important;
    }

    &__date {
        display: block;
        font-size: 12px;
        line-height: 14px;
        color: #4A4A4A;
        margin-bottom: 4px !important;
        padding-left: 5px;
        text-align: left;
    }

    &__price {
        font-size: 17px;
        padding-left: 5px;
        font-weight: 700;
        line-height: 20px;
        color: #000000;
        margin-top: 0;
    }

}