.addProduct {
    margin-top: 60px;
    padding-bottom: 100px;

    &__title {
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        margin-bottom: 10px !important;
    }

    &__subtitle {
        font-weight: 500;
        font-size: 16px;
        line-height: 19px;
        text-align: center;
        margin-bottom: 42px !important;
    }

    &__card-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 21px;
    }

    &__card {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        opacity: .5;
    }

    &__single-card {
        position: relative;
        cursor: pointer;

        &::before {
            content: "";
            position: absolute;
            top: -10px;
            right: 0;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/perfect.svg);
            background-size: 16px 12px;
            background-position: 50%;
            background-color: #fff;
            border: 1px solid #41d481;
            box-shadow: 1px 3px 6px rgba(0, 0, 0, .086);
        }
    }

    &__edit {
        display: flex;
        align-items: center;
        width: 100%;
        border: 1px solid hsla(0, 0%, 59.2%, .496575);
        padding: 13px 24px;
        box-sizing: border-box;
        border-radius: 5px;
        box-shadow: 12px 4px 24px rgba(0, 0, 0, .06);
        position: relative;

        &:before {
            content: "";
            position: absolute;
            top: -11px;
            left: 13.5%;
            margin: auto;
            width: 0;
            height: 0;
            border-left: 10px solid transparent;
            border-right: 10px solid transparent;
            border-bottom: 10px solid rgba(0, 0, 0, .209296);
        }
    }

    &__edit-title {
        font-weight: 500;
        line-height: 19px;
        color: #000;
        margin-right: 15px !important;
        font-size: 18px;
    }

    &__edit-address {
        font-weight: 600;
        line-height: 19px;
        font-size: 16px;
        color: #19d476;
        margin-right: 13px !important;
    }

    &__edit-btn {
        position: relative;
        background-color: white;
        border: 1px solid #41d481;
        border-radius: 100px 5px 5px 100px;
        padding: 8px 19px 10px 47px;
        font-weight: 500;
        line-height: 19px;
        cursor: pointer;
        margin-left: 20px;

        &::before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 37px;
            height: 37px;
            border-radius: 50%;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/edit_icon.svg);
            background-size: 17px 17px;
            background-position: 50%;
            background-color: #41d481;
        }
    }

    &__form {
        margin-top: 58px;
        outline: none;
    }

    &__form-top {
        display: flex;
        align-items: flex-end;
        margin-bottom: 48px;
    }

    &__label {
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        color: #000;
        margin-bottom: 13px !important;
    }

    &__label-wrapper {
        display: flex;
        flex-direction: column;
    }

    &__input {
        border: none;
        outline: none;
        border-bottom: 1px solid #979797;
        padding-bottom: 7px;
        margin-right: 43px;
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        color: black;

        &::-webkit-input-placeholder {
            font-weight: 500;
            font-size: 19px;
            line-height: 23px;
            color: black;
        }
    }

    &__select {
        border: none;
        border-bottom: 1px solid #979797;
        padding-bottom: 6px;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #000;
        margin-right: 26px;
    }

    &__label-input {
        display: flex;
        flex-direction: column;
        margin-bottom: 31px;
    }

    &__input-check {
        width: 24px;
        height: 24px;
        margin-right: 11px;
    }

    &__check-div {
        display: flex;
        width: 33%;
        margin-bottom: 33px;
    }

    &__label-check {
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #000;
    }

    &__block {
        margin-bottom: 48px;
    }

    &__block-top {
        display: flex;
    }

    &__select-block {
        display: flex;
        margin-bottom: 48px;
    }

    &__block-info {
        display: flex;
        flex-direction: column;
    }

    &__block-bottom {
        display: flex;
    }

    &__middle-heading {
        font-weight: 500;
        font-size: 19px;
        line-height: 23px;
        color: #000000;
        padding-bottom: 133px;
    }
}

.form-bottom {
    padding-top: 10px;
    border-top: 1px solid #979797;
}

.img-loader-block-top {
    display: flex;
    justify-content: space-between;
}

.img-loader-block .img-loader {
    margin-bottom: 23px;
}

.img-loader-block .img-loader input {
    display: none;
}

.img-loader-block .img-loader label {
    background-color: rgba(180, 226, 225, .350232);
    border: 1px solid #50e3c2;
    box-sizing: border-box;
    border-radius: 9px;
    width: 107px;
    height: 107px;
    position: relative;
    display: block;
}

.img-loader-block .img-loader label:before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
    left: 0;
    margin: auto;
    width: 42px;
    height: 36px;
    background-repeat: no-repeat;
    background-image: url(./../../assets/images/camera.png);
}

.additional-footer {
    margin-top: 20px;
}

.additional-footer__title {
    margin-bottom: 15px !important;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: #000;
}

.iframe-block {
    margin-bottom: 22px !important;
}


.label-input input {
    width: 300px;
}

.flex-blockk {
    display: flex;
    align-items: center;
}

.add-new {
    background-color: #19d476;
    border-radius: 13px;
    padding: 21px 46px;
    margin-right: 67px;
    font-style: normal;
    font-weight: 500;
    font-size: 19px;
    line-height: 23px;
    color: #000;
    border: none;
    outline: none;
    cursor: pointer;
}

.add-new-view {
    font-style: normal;
    font-weight: 500;
    font-size: 19px;
    line-height: 23px;
    text-decoration-line: underline;
    color: #4e72ca;
}