.ads {
    &__wrapper {
        margin-top: -60px;
        padding-top: 32px;
        border-bottom: 1px solid hsla(0, 0%, 59.2%, .393739);
    }

    &__top {
        width: 100%;
        border: 1px solid hsla(0, 0%, 59.2%, .496575);
        padding: 16px 16px 16px 58px;
        box-sizing: border-box;
        background: #fff;
        border-radius: 6px;
        position: relative;
        margin-bottom: 12px;

        &::before {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 17px;
            margin: auto;
            width: 23.8px;
            height: 24.72px;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/lupa_icon.svg);
        }
    }

    &__top-input {
        width: 100%;
        border: none;
        outline: none
    }

    &__top-navigate {
        display: flex;
        align-items: center;
        margin-bottom: 25px
    }

    &__top-btn {
        font-style: normal;
        font-weight: 700;
        font-size: 13px;
        line-height: 16px;
        color: #704fff;
        background: #e5e3ff;
        border-radius: 3px;
        padding: 3px 11px;
        margin-right: 13px;
        border: none;
        cursor: pointer;

        &:last-child {
            margin-right: 0
        }
    }

    &__top-info {
        display: flex;
        align-items: center;
        margin-bottom: 23px;
    }

    &__top-price {
        display: flex;
        margin-bottom: 30px;
    }

    &__info-left {
        margin-right: 54px;
    }

    &__left-title {
        font-size: 16px;
        line-height: 19px;
        letter-spacing: -6.7332e-05px;
        color: #787993;
    }

    &__top-right {
        display: flex;
        -webkit-align-items: center;
        align-items: center;
        background: #fff;
        border: 1px solid hsla(0, 0%, 59.2%, .496575);
        border-radius: 27px;
        padding: 8px;
        box-sizing: border-box;
        width: 472px;
        position: relative;

        &::after {
            content: "";
            position: absolute;
            right: 24px;
            width: 18px;
            height: 11px;
            top: 0;
            bottom: 0;
            margin: auto;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/ads_bottom-arrow.svg)
        }
    }

    &__right-info {
        background-color: #ebf5ff;
        border: 1px solid #b3d9ff;
        box-sizing: border-box;
        border-radius: 20.5px;
        overflow: hidden;
        padding: 11px 47px 12px 20px;
        margin-right: 7px;
        cursor: pointer;
        position: relative;

        &:after {
            content: "";
            position: absolute;
            right: -2px;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 42px;
            height: 42px;
            border-radius: 50%;
            background: #ebf5ff;
            border: 1px solid #b3d9ff;
            box-sizing: border-box;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/cross.svg);
            background-position: 50%
        }

    }

    &__right-title {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #2979ff;
    }

    &__price-left {
        margin-right: 41px;
    }

    &__price-right {
        position: relative;
    }

    &__price-range--wrapper {
        position: absolute;
    }

    &__left-title {
        width: 250px;
        line-height: 19px;
        letter-spacing: -.000067332px;
        color: #787993;
    }

    &__form {
        display: flex;
        justify-content: space-between;
        padding-bottom: 30px
    }

    &__form-left {
        display: flex;
    }

    &__form-select {
        border: none;
        outline: none;
        background: none;
        margin-right: 70px;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #414d6a;

        &:last-child {
            margin-right: 0;
        }
    }

    &__form-btn {
        font-weight: 700;
        font-size: 15px;
        line-height: 19px;
        text-align: center;
        color: #fff;
        padding: 12px 18px;
        background: #304fff;
        box-shadow: 3px 6px 14px #9cc1ff;
        border-radius: 22px;
        border: none;
        cursor: pointer;
    }

    &__bottom-card--heading {
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        color: #41D481;
        margin-bottom: 18px;
        padding-left: 15px;
    }
}


.top {
    color: #57b4e2;
    background: #e4f6ff
}

.perfect {
    color: #47b95e;
    background: #d2ffdb
}

.g-price {
    color: #874c1a;
    background: #f6dac2
}

.place-town {
    color: #787993;
    background: #d8d8d8;
    padding-right: 31px;
    position: relative
}

.place-town:after {
    content: "";
    position: absolute;
    right: 10px;
    width: 7px;
    height: 10px;
    top: 0;
    bottom: 0;
    margin: auto;
    background-repeat: no-repeat;
    background-image: url(./../../assets/images/location.svg)
}


input[type=range] {
    border: 1px solid white;
    width: 860px;
    position: absolute;
    left: 0;
    top: 8px;
}
