.singlePage {


    &__top {
        display: flex;
        gap: 12px;
    }

    &__top-navigate {
        margin-right: 20px;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #787993;
        cursor: pointer;
    }


    &__center-title {
        font-weight: 500;
        font-size: 34px;
        line-height: 41px;
        color: #414d6a;
        margin-bottom: 14px;
    }

    &__product {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;

        img {
            display: block;
        }
    }

    &__top-heading {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -.0000589155px;
        color: #414d6a;
    }

    &__product-right {
        width: 45%;
        margin-top: 80px;
    }

    &__right-top {
        display: flex;
        justify-content: space-between;
        margin-bottom: 17px;
    }

    &__top-price {
        font-weight: 500;
        font-size: 40px;
        line-height: 49px;
        letter-spacing: -.00016833px;
        color: #444;
        margin-bottom: 8px;
    }

    &__top-link {
        display: block;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -.0000589154px;
        text-decoration: underline;
        color: #4e72ca;
        margin-bottom: 14px;
    }

    &__customer-info {
        max-width: 347px;
        width: 100%;
        margin-bottom: 7px;
        display: flex;
        background: #65e99f;
        align-items: center;
        border-radius: 18.5px;
        padding: 0 0 0 23px;
    }

    &__info-phone {
        font-weight: 700;
        font-size: 16px;
        line-height: 20px;
        letter-spacing: -.0000673319px;
        color: #000;
        margin: 12px;
    }

    &__info-btn {
        border: none;
        background-color: #41d481;
        border-radius: 18.5px;
        padding: 9px 26px 10px 30px;
        margin-left: 10px;
        cursor: pointer;
    }

    &__auth-info {
        display: flex;
        align-items: center;
        margin-bottom: 24px;
    }

    &__auth-heading {
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -.0000317237px;
        color: #000;
        margin-right: 7px !important;
    }

    &__auth-title {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: -.0000413787px;
        text-decoration-line: underline;
        color: #2aae63;
    }

    &__contact-info {
        margin-bottom: 25px;
    }

    &__contact-btn {
        background-color: #304fff;
        padding: 15px 28px;
        margin-right: 11px;
        border-radius: 23.5px;
        border: none;
        outline: none;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -.0000589155px;
        color: white;
        cursor: pointer;
    }

    &__table {
        display: flex;
    }

    &__table-left {
        border-left: 1px solid hsla(0, 0%, 59.2%, .191604);
    }

    &__table-item {
        border-right: 1px solid hsla(0, 0%, 59.2%, .191604);
        border-bottom: 1px solid hsla(0, 0%, 59.2%, .191604);
        padding-right: 0;
        padding: 19px 29px 21px 15px;
    }

    &__table-title {
        width: 115px;
        font-size: 14px;
        line-height: 16px;
        letter-spacing: -.00016833px;
        color: #444;
        margin-bottom: 5px;
        width: 140px;
    }

    &__table-text {
        width: 140px;
        font-size: 14px !important;
        font-weight: 500;
        letter-spacing: -.00016833px;
        color: #444;
        margin: 0;
    }

    &__table-bottom {
        margin-bottom: 10px;
        border-left: 1px solid hsla(0, 0%, 59.2%, .191604);
    }

    &__about-text {
        font-weight: 500;
        font-size: 17px;
        line-height: 25px;
        letter-spacing: -.0000385217px;
        color: #414d6a;
        margin-bottom: 18px !important;
    }

    &__product-left {
        width: 553px;
        height: auto;
        // // object-fit: contain;   
    }
}

.activeBTN {
    background-color: #4e72ca;
}

.special {
    display: flex;
    width: 100%;
    font-weight: 500;
    font-size: 18px !important;
    line-height: 22px;
    letter-spacing: -0.00016833px;
    text-decoration-line: underline;
    color: #4E72CA;
    padding-right: 35px;
}


.add-info {
    margin-bottom: 97px;
    display: flex;
    justify-content: space-between;
}

.add-info__left {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: -.0000589155px;
    color: #000;
}

.add-info__right {
    font-weight: 600;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: -.0000589155px;
    color: red;
}