.lookingSlider {
    padding-bottom: 30px;
    &__wrapper {
        max-width: 250px;
        width: 100%;
        padding: 20px;
        position: relative;
    }

    &__title {
        font-weight: 700;
        font-size: 20px;
        line-height: 25px;
        color: #414D6A;
        margin-bottom: 25px;
        &::before {
            content: "";
            width: 17px;
            height: 17px;
            padding-right: 30px;
            display: inline-block;
            background-image: url(./../../assets/images/lupa_icon.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
        }
    }

    &__heading {
        font-weight: 700;
        font-size: 14px;
        line-height: 24px;
        color: #4E72CA;
    }

    &__text {
        font-weight: 700;
        font-size: 14px;
        line-height: 24px;
        color: #AAAAB6;
        padding-left: 15px;
        text-align: start !important;
        left: 10px;
        &::before {
            content: "";
            width: 12px;
            height: 12px;
            position: absolute;
            top: calc(50% + 6%);
            left: 18px;
            display: inline-block;
            background-image: url(./../../assets/images/lupa_icon.svg);
            background-repeat: no-repeat;
            background-position: center;
            background-size: contain;
        }
    }
}