.profileCard {
    background-color: #fff;
    margin-bottom: 10px;

    &__top {
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 23px 16px 10px 17px;
        box-sizing: border-box;
        border-bottom: 1px solid #9797973f;
    }

    &__top-img {
        border: 1px solid #9797974a;
        width: 49px;
        height: 49px;
        object-fit: cover;
        border-radius: 50%;
    }

    &__top-title {
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #414d6a;
    }

    &__price-title {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #414d6a;
    }


    &__date {
        width: 10%;
        margin-right: 20px !important;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414d6a;
    }

    &__date-item {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #414d6a;
        margin-right: 20px !important;
    }

    &__btn {
        background-color: #fff;
        border: 1px solid #41d481;
        border-radius: 100px 5px 5px 100px;
        padding: 8px 20px 10px 47px;
        font-weight: 500;
        line-height: 19px;
        color: #000;
        position: relative;
        cursor: pointer;

        &::before {
            width: 38px;
            height: 38px;
            content: "";
            position: absolute;
            left: 0;
            background-color: #41d481;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/profile_arrow.svg);
            border-radius: 50%;
            top: 0;
            bottom: 0;
            margin: auto;
            background-size: 26px 16px;
            background-position: 50%;
        }
    }

    &__more-img {
        cursor: pointer;
        width: 35px;
        height: 35px;
        background-color: #FFFFFF;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.196643);
        border-radius: 50%;
        position: relative;
    }

    &__more-bg {
        position: absolute;
        top: 16px;
        left: 8px;
    }

    &__bottom {
        display: flex;
        align-items: center;
        padding: 8px 22px;
    }

    &__bottom-stat {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #304fff;
        margin-right: 20px !important;
    }

}

.profileItem {
    padding-bottom: 76px;
    background-color: rgba(0, 0, 0, 0.0470165);
}

.btn--added {
    border: 1px solid #304fff;
}

.btn--added:before {
    background-color: #304fff;
}