.profile {
    padding-top: 39px !important;
    background-color: #fff;
    padding-bottom: 16px;
    border-bottom: 1px solid #979797;
    margin-top: -60px;

    &__header-top {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__left-title {
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #414d6a;
    }

    &__header-right {
        display: flex;
        align-items: center;
    }

    &__right-title {
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #304fff;
        margin-right: 26px !important;
    }

    &__header-right--btn {
        background-color: #fff;
        border: 1px solid #41d481;
        box-sizing: border-box;
        border-radius: 100px 5px 5px 100px;
        padding: 8px 22px 10px 47px;
        font-weight: 500;
        line-height: 19px;
        color: #000;
        position: relative;
        cursor: pointer;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            bottom: 0;
            margin: auto;
            width: 36px;
            height: 36px;
            border-radius: 50%;
            background: #41d481;
            background-repeat: no-repeat;
            background-size: 15px 15px;
            background-position: 50%;
            background-image: url(./../../assets/images/add_logo.svg)
        }
    }

    &__link-wrapper {
        padding-top: 16px;
        border-bottom: 1px solid hsla(0, 0%, 59.2%, .600459);
        display: flex;
        margin-bottom: 5px;
    }

    &__link-navlink {
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: #000;
        margin-right: 50px;
        display: block;
        padding-bottom: 14px;

        &:last-child {
            margin-right: 0;
        }
    }
    &__main {
        background-color: rgba(0, 0, 0, .04701);
    }

    &__main-info {
        padding-top: 15px;
        padding-bottom: 11px;
    }

    &__main-menu {
        display: flex;
        margin-bottom: 11px;
    }

    &__menu-title {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #000;

        &:first-child {
            margin-right: 70px !important;
        }

        &:nth-child(2) {
            margin-right: 180px !important;
        }

        &:nth-child(3) {
            margin-right: 100px !important;
        }

        &:nth-child(4) {
            margin-right: 100px !important;
        }
    }
}
