.header {
    background-color: white;
    box-shadow: 12px 4px 24px rgb(0 0 0 / 6%);
    padding: 15px 0;
    margin-bottom: 60px;
 
    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 16px;
    }

    &__info {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__info-text {
        max-width: 152px;
        width: 100%;
        font-weight: 700;
        font-size: 15px;
        line-height: 17px;
        margin-left: 14px !important;
    }

    &__navbar {
        display: flex;
        align-items: center;
    }

    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
        gap: 30px;
    }

    &__item-link {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000000;
        text-decoration: none;
    }

    &__lang-btn {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #4A4A4A;
        border: none;
        outline: none;
        background: none;
        cursor: pointer;
    }

    &__lang-line {
        display: inline-block;
        width: 1px;
        height: 10px;
        background-color: #4A4A4A;
    }

    &__right-wrapper {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    &__right-auth {
        position: relative;
        display: flex;
        align-items: center;
        gap: 10px;
        transition: all 0.9s ease;

    }

    &__right-title {
        max-width: 100px;
        width: 100%;
        font-size: 16px;
        color: #787993;
    }

    &__right-login {
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        color: #4E72CA;
        margin-right: 15px;
    }

    &__right-img {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        margin-left: 15px;
        margin-right: 15px;
        display: block;
        border: 2px solid;
        border-image-source: linear-gradient(to left, #743ad5);
    }

    &__right-sign {
        font-weight: 700;
        font-size: 17px;
        line-height: 21px;
        color: #4E72CA;
        margin-left: 10px;

    }

    &__right-line {
        display: inline-block;
        width: 2px;
        height: 15px;
        background-color: #4E72CA;
    }

    &__log-out {
        min-width: 90px;
        display: none;
        padding: 10px 10px;
        border: none;
        outline: none;
        color: rgb(255, 255, 255);
        background-color: #111;
        cursor: pointer;
        position: relative;
        z-index: 0;
        border-radius: 10px;
        user-select: none;
        -webkit-user-select: none;
        touch-action: manipulation;

        &::before {
            content: "";
            background: linear-gradient(45deg,
                    #ff0000,
                    #ff7300,
                    #fffb00,
                    #48ff00,
                    #00ffd5,
                    #002bff,
                    #7a00ff,
                    #ff00c8,
                    #ff0000);
            position: absolute;
            top: -2px;
            left: -2px;
            background-size: 400%;
            z-index: -1;
            filter: blur(5px);
            -webkit-filter: blur(5px);
            width: calc(100% + 4px);
            height: calc(100% + 4px);
            animation: glowing-log-out 20s linear infinite;
            transition: opacity 0.3s ease-in-out;
            border-radius: 10px;
        }

        &:after {
            z-index: -1;
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            background: #222;
            left: 0;
            top: 0;
            border-radius: 10px;
        }

        @keyframes glowing-log-out {
            0% {
                background-position: 0 0;
            }

            50% {
                background-position: 400% 0;
            }

            100% {
                background-position: 0 0;
            }
        }
    }

    &__bottom {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__bottom-left {
        background-color: white;
        border: 1px solid hsla(0, 0%, 59.2%, .496575);
        border-radius: 22.5px;
        overflow: hidden;
    }

    &__bottom-select {
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: black;
        padding: 13px 44px 14px 30px;
        outline: none;
        border: none;
        border-right: 1px solid hsla(0, 0%, 59.2%, .496575);
    }

    &__bottom-input {
        border: none;
        font-weight: 500;
        font-size: 15px;
        line-height: 18px;
        color: #000;
        outline: none;
        padding-left: 25px;
    }

    &__bottom-btn {
        outline: none;
        cursor: pointer;
        border: 1px solid transparent;
        background-color: #19d476;
        border-radius: 22px;
        padding: 14px 19px;
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: white;
    }

    &__right-btn {
        position: relative;
        background-color: #19d476;
        border: none;
        outline: none;
        border-radius: 22px;
        padding: 12px 30px 13px 56px;
        font-weight: 700;
        font-size: 15px;
        line-height: 19px;
        cursor: pointer;
        color: white;

        &:before {
            content: "";
            position: absolute;
            left: 0;
            top: 0;
            margin: auto;
            bottom: 0;
            background: #11b463;
            width: 44px;
            height: 44px;
            border-radius: 50%;
            background-repeat: no-repeat;
            background-image: url(./../../assets/images/add_logo.svg);
            background-position: 50%
        }
    }
}

.next {
    border-left: 1px solid hsla(0, 0%, 59.2%, .496575);
    border-right: none;
}

.header__right-wrapper {
   position: relative;
}

.header__right-wrapper:hover .header__log-out {
    display: block;
    position: absolute;
    top: 10px;
    left: 100%;
    z-index: 111;
}

.lang-active {
    background-image: url('./../../assets/images/lang_line.svg');
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
    z-index: 1;
}
