.login {

    &__wrapper {
        display: flex;
        justify-content: center;
        height: 812px;
        gap: 114px;
    }

    &__left {
        margin-top: 121px;
    }

    &__left-title {
        font-style: normal;
        font-weight: 700;
        font-size: 24px;
        line-height: 30px;
        text-align: center;
        text-transform: uppercase;
        color: #000;
        margin-bottom: 22px !important;
    }

    &__left-text {
        max-width: 334px;
        width: 100%;
        font-weight: 500;
        font-size: 17px;
        line-height: 23px;
        text-align: center;
        color: #304FFF;
        margin-left: 30px !important;
        margin-bottom: 11px !important;
    }

    &__form {
        display: flex;
        flex-direction: column;
        margin-left: 30px;
    }

    &__form-label {
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        color: #787993;
        margin-bottom: 7px !important;
    }

    &__form-input {
        background-color: white;
        border: 1px solid hsla(0, 0%, 59.2%, .482592);
        border-radius: 5px;
        width: 329px;
        padding: 10px 27px;
        margin-bottom: 28px;
        outline: none;
    }

    &__form-btn {
        width: 329px;
        padding: 21px 0;
        background-color: #41d481;
        border-radius: 5px;
        font-weight: 500;
        font-size: 17px;
        line-height: 21px;
        text-align: center;
        color: white;
        border: none;
        outline: none;
        cursor: pointer;
        margin-bottom: 49px;
    }

    &__right-title {
        color: #41D481;
        font-size: 36px;
        letter-spacing: 2px;
        font-weight: 900;
    }

    &__right {
        position: relative;
        width: 50%;
        height: 100%;
        background-image: url(./../../assets/images/login_bg.png);
        background-repeat: no-repeat;
        background-size: cover;
    }

    &__right-wrapper {
        position: absolute;
        width: 285px;
        padding: 79px 40px 40px 41px;
        background-color: white;
        border-radius: 220px 220px 220px 25px;
        left: 61px;
        bottom: 165px;
    }

    &__right-img {
        margin-bottom: 8px !important;
        width: 50px;
        height: 26px;
    }

    &__right-text {
        width: 213px;
        font-size: 19px;
        line-height: 21px;
        color: #787993;
        margin-bottom: 19px !important;
    }

    &__right-btn {
        border: 2px solid #41d481;
        border-radius: 5px;
        background-color: white;
        padding: 9px 18px 11px 19px;
        font-weight: 700;
        line-height: 20px;
        color: #41d481;
        outline: none;
        cursor: pointer;
    }

    &__password {
        width: 329px;
        font-weight: 700;
        line-height: 20px;
        text-align: center;
        color: #4e72ca;
        display: block;
        margin-left: 30px !important;
    }
}

.login__left .login__or {
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    color: #787993;
    position: relative;
    width: 329px;
    margin-bottom: 26px !important;
    margin-left: 30px !important;
}

.login__left .login__or:before {
    left: 0
}

.login__left .login__or:after,
.login__left .login__or:before {
    content: "";
    width: 140px;
    height: 2px;
    background-color: #979797;
    top: 0;
    bottom: 0;
    margin: auto;
    position: absolute
}

.login__left .login__or:after {
    right: 0
}