.swiper {
    padding: 48px 0;
    background-color: rgba(182, 202, 251, 0.159986);
    box-shadow: inset 0px 21px 13px rgba(182, 202, 251, 0.225402);

    &__wrapper {
       div {
        display: flex;
        align-items: center;
        gap: 80px;
        cursor: pointer;
       }
    }
}