/* Gilroy Font */

@font-face {
  font-family: 'Gilroy';
  src: url('./../src/assets/fonts/Gilroy-Regular.woff') format('woff2'),
       url('./../src/assets/fonts/Gilroy-Regular.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url('./../src/assets/fonts/Gilroy-Bold.woff2') format('woff2'),
       url('./../src/assets/fonts/Gilroy-Bold.woff') format('woff');
  font-weight: bold;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url('./../src/assets/fonts/Gilroy-Black.woff2') format('woff2'),
       url('./../src/assets/fonts/Gilroy-Black.woff') format('woff');
  font-weight: 900;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url('./../src/assets/fonts/Gilroy-Semibold.woff2') format('woff2'),
       url('./../src/assets/fonts/Gilroy-Semibold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
}

@font-face {
  font-family: 'Gilroy';
  src: url('./../src/assets/fonts/Gilroy-Medium.woff2') format('woff2'),
       url('./../src/assets/fonts/Gilroy-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
