@font-face {
  font-family: 'Gilroy';
  src: url('./../src/assets/fonts/Gilroy-Extrabold.woff2') format('woff2'),
       url('./../src/assets/fonts/Gilroy-Extrabold.woff') format('woff');
  font-weight: 800;
  font-style: normal;
}

/* General Styles */

html {
  height: 100%;
  scroll-behavior: smooth;
}

*,
::before,
::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  font-family: 'Gilroy', 'Arial', sans-serif;
}

.container {
  max-width: 1185px;
  width: 100%;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
}

ul,
ol,
li {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0 !important;
  padding: 0;
}

img {
  vertical-align: middle;
}

a {
  text-decoration: none;
}