.card {
    display: flex;
    flex-direction: column;
    align-items: center;

    &__img {
        width: 75px;
        height: 75px;
        border-radius: 20px;
        object-fit: contain;
        display: flex;
        align-items: center;
        justify-content: center;
        vertical-align: middle;
    }

    &__text {
        font-weight: 700;
        font-size: 14px;
        line-height: 17px;
        color: #4A4A4A;
        margin-top: 6px !important;
    }
}

.primary {
    background-color: #B6CAFB;
}

.orange {
    background-color: #ff9e83;
}

.cyan {
    background-color: #B4E2E1;
}

.yellow {
    background-color: #F7E233;
}

.lightOrange {
    background-color: #F6DAC2;
}